import React from "react"
import './React.css'
import {Tile} from '../../tile/Tile'
import {reactData} from '../../../data/react'


export class Reactt extends React.Component {
	state = {
		news: []
	}

	componentWillMount() {
		this.setState({
			news: reactData
		})
	}

	render() {
		return (
			<div className="React">
				{this.state.news.map((item => <Tile key={item.id} data={item}/>))}
			</div>
		);
	}
}



