import React from "react"
import './Angular.css'
import {Tile} from '../../tile/Tile'
import {angularData} from '../../../data/angular'

export class Angular extends React.Component {
	state = {
		news: []
	}

	componentWillMount() {
		this.setState({
			news: angularData
		})
	}
	render() {
		return (
			<div className="Angular">
			{this.state.news.map((item => <Tile key={item.id} data={item}/>))}
			</div>
		);
	}
}



