import React from "react"
import './Node.css'
import {Tile} from '../../tile/Tile'
import {nodeData} from '../../../data/node'

export class Node extends React.Component {
	state = {
		news: []
	}

	componentWillMount() {
		this.setState({
			news: nodeData
		})
	}

	render() {
		return (
			<div className="Node">
			{this.state.news.map((item => <Tile key={item.id} data={item}/>))}
			</div>
		);
	}
}



