import React from 'react'
import './Sidebar.css'
import {
	Link
} from 'react-router-dom'

export class Sidebar extends React.Component {
	render() {
		return (
			<div className="Sidebar col-md-2">
				<div className="row top-bar">
					<div className="col-md-3">
						<img alt="" src="https://media.licdn.com/mpr/mpr/shrinknp_400_400/AAIA_wDGAAAAAQAAAAAAAAxpAAAAJDlhYTI5YWExLTkzYTMtNDNlYS1iY2M5LTMwYzE1ZGQ2NmMzNw.jpg"
						className="img-responsive img-circle"/>
					</div>
					<div className="col-md-9 info">
						<div>Marjo Ballabani</div>
						<div>@marjoballabani</div>
					</div>
				</div>
				<input type="text" className="search" placeholder="Search ..."/>
				<hr/>
					<div className="menu-container">
						<Link to="/" className="menu-item">
							<i className="devicon-git-plain colored"></i>
							<label>News</label>
						</Link>
						<Link to="/react" className="menu-item">
							<i className="devicon-react-original-wordmark colored"></i>
							<label>React</label>
						</Link>
						<Link to="/angular" className="menu-item">
							<i className="devicon-angularjs-plain colored"></i>
							<label>Angular</label>
						</Link>
						<Link to="/node" className="menu-item">
							<i className="devicon-nodejs-plain colored"></i>
							<label>Node.js</label>
						</Link>
						<Link to="/javascript" className="menu-item">
							<i className="devicon-javascript-plain colored"></i>
							<label>Javascript</label>
						</Link>
						<Link to="/sass" className="menu-item">
							<i className="devicon-sass-original colored"></i>
							<label>Sass</label>
						</Link>
						<Link to="/sass" className="menu-item">
							<i className="devicon-html5-plain-wordmark colored"></i>
							<label>Html</label>
						</Link>
						<Link to="/sass" className="menu-item">
							<i className="devicon-css3-plain-wordmark colored"></i>
							<label>Css</label>
						</Link>
					</div>
			</div>
		);
	}
}



