import React from 'react'
import {
	Route,
	Switch
} from 'react-router-dom'
import './Wrapper.css'
import {Reactt, Node, Angular } from '../pages/'
import RenderGist from 'react-render-gist'


export class Wrapper extends React.Component {
	render() {
		return (
			<div className="Wrapper col-md-10">
				<div className="col-md-12 content">
				<Switch>
					<Route exact path="/" render={(props) => <RenderGist gist="022e04e7650b1a763ca74425b60d9ea2" />} />
					<Route path="/react" render={props => <Reactt />} />
					<Route path="/angular" render={props => <Angular />} />
					<Route path="/node" render={props => <Node />} />
				</Switch>
				</div>
			</div>
		);
	}
}



